.catalog {
  padding: 16px 0 112px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 24px / 4));
    gap: 8px;
  }
  &__btn {
    height: 56px;
    padding: 5px 24px;
    margin: 32px auto 0;
    span {
      font-size: 16px;
    }
  }
}
.catalog-card {
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 12px;
  padding: 16px 24px 24px;
  &__img {
    height: 132px;
    display: flex;
    justify-content: center;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 12px;
  }
  &__title {
    font-size: 13px;
    line-height: 150%;
    color: #707680;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:1;
    -webkit-box-orient: vertical;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    margin-top: 8px;
    margin-bottom: auto;
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 8px;
    margin-top: 16px;
  }
  .cartcalc {
    max-width: 92px;
    width: 0;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    background: #F2F4F7;
    border-radius: 8px;
    padding: 5px 0;
    margin-left: -8px;
    overflow: hidden;
    transition: all 0.3s;
    input {
      width: 24px;
      height: 24px;
      text-align: center;
      background: transparent;
      border: none;
      padding: 0;
      font-size: 14px;
      line-height: 120%;
    }
  }
  .cartcalc--active {
    width: 100%;
    padding: 5px 10px;
    margin-left: 0;
  }
  .ccalc-minus,
  .ccalc-plus {
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    cursor: pointer;
    svg {
      max-width: 100%;
    }
  }
  &__btn {
    height: 44px;
    flex-grow: 1;
    background: transparent;
    border: 1px solid #A7ABB2;
    border-radius: 8px;
    padding: 5px 10px;
    span {
      font-size: 16px;
      line-height: 120%;
    }
    span:nth-child(2) {
      display: none;
    }
  }
  .btn-yellow {
    background: #FFE500;
    border-color: transparent;
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: block;
    }
  }
}

@media screen and (max-width: 1100px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 16px / 3));
    }
  }
  .catalog-card {
    &:nth-child(18) ~ .catalog-card {
      display: none;
    }
    &__img {
      height: 164px;
    }
  }
}

@media screen and (max-width: 900px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
    }
  }
  .catalog-card {
    &:nth-child(8) ~ .catalog-card {
      display: none;
    }
    &__img {
      height: 164px;
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    padding: 10px 0 48px;
  }
  .catalog-card {
    &__img {
      height: 124px;
    }
    &__text {
      font-size: 14px;
    }
    &__btn {
      span {
        font-size: 14px;
      }
    }
    @media screen and (min-width: 451px) {
      &__btns {
        flex-direction: column;
      }
      .cartcalc {
        max-width: 100%;
        width: 100%;
        height: 0;
        padding: 0 10px;
        margin: 0 !important;
      }
      .cartcalc--active {
        height: 48px;
        padding: 5px 10px;
      }
      &__btn {
        width: 100%;
        height: 48px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .catalog {
    &__cards {
      grid-template-columns: 100%;
    }
    &__btn {
      width: 100%;
    }
  }
  .catalog-card {
    &:nth-child(7) ~ .catalog-card {
      display: none;
    }
  }
}