h2 {
  font-size: 28px;
  line-height: 140%;
  font-weight: 600;
}
h3 {
  font-size: 20px;
  line-height: 140%;
  font-weight: 600;
}
.btn-yellow {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #FFE500;
  border-radius: 8px;
  &:hover {
    background: #FFED4D;
  }
  i {
    max-width: 20px;
    img {
      max-width: 100%;
    }
  }
  span {
    line-height: 150%;
    color: #21262E;
  }
}
.breadcrumbs {
  padding: 24px 0;
  .container {
    padding: 0;
  }
  &__slider {
    position: relative;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    position: relative;
    width: auto !important;
    height: auto;
    &:first-child {
      margin-left: 30px;
    }
    &:last-child {
      padding-right: 32px;
      margin-right: 0 !important;
    }
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -20px;
        transform: translate(0, -50%);
        width: 5px;
        height: 10px;
        background-image: url("../img/arrow-right.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        pointer-events: none;
      }
    }
  }
  &__item {
    font-size: 14px;
    line-height: 140%;
    color: #707680;
    &:hover {
      color: #21262E;
      svg path {
        stroke: #21262E;
      }
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__item--active {
    color: #21262E;
  }
}
.page-top {
  padding: 20px 0 16px;
  &__group,
  &__group2 {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 12px 16px;
  }
  &__title {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    gap: 8px 16px;
    span {
      font-size: 16px;
      color: #707680;
    }
  }
  .search {
    position: relative;
    flex-shrink: 0;
    input {
      width: 320px;
      height: 40px;
      background: #fff;
      border: 1px solid transparent;
      border-radius: 8px;
      padding: 0 14px 0 44px;
      font-size: 14px;
      line-height: 170%;
      color: #21262E;
      &::placeholder {
        color: #707680;
      }
    }
    &-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 14px;
      transform: translate(0, -50%);
      width: 20px;
      background: transparent;
      padding: 0;
      svg {
        width: 20px;
        height: auto;
      }
    }
  }
  &__group2 {
    align-items: center;
    background: #fff;
    border-radius: 12px;
    padding: 16px 24px;
    margin-top: 32px;
  }
  &__selections {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 8px;
  }
  &__select {
    position: relative;
    &--active {
      .page-top__select-hidden {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &-show {
      position: relative;
      display: flex;
      align-items: center;
      gap: 8px;
      background: #F2F4F7;
      border-radius: 4px;
      padding: 4px 12px;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        bottom: -8px;
        left: 0;
        width: 100%;
        height: 8px;
        background: transparent;
      }
      i {
        width: 10px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        svg {
          width: 100%;
          height: auto;
          transition: all 0.3s;
        }
        svg path {
          transition: all 0.3s;
        }
      }
      span {
        font-size: 14px;
        line-height: 170%;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 3;
      top: calc(100% + 8px);
      left: 0;
      width: 320px;
      background: #fff;
      box-shadow: 0px 12px 24px 0px rgba(33, 38, 46, 0.16);
      padding: 20px 16px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-color: #FFE500;
        background-image: url("../img/check.svg");
        border-color: transparent;
      }
    }
    label {
      position: relative;
      display: block;
      font-size: 14px;
      line-height: 170%;
      padding-left: 30px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1.5px solid #A7ABB2;
        border-radius: 4px;
        transition: all 0.3s;
      }
    }
  }
  &__select2 {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__select2-title {
    font-size: 14px;
    line-height: 140%;
    color: #707680;
  }
  .nice-select {
    width: auto;
    height: auto;
    display: flex;
    align-items: center;
    gap: 8px;
    background: transparent;
    border: none;
    padding: 0 18px 0 0;
    font-size: 14px;
    line-height: 140%;
    font-weight: 400;
    color: #21262E;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 10px;
      height: 5px;
      background-image: url("../img/arrow-bottom.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    .list {
      width: 320px;
      left: unset;
      right: 0;
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      background: #fff;
      box-shadow: 0px 12px 24px 0px rgba(33, 38, 46, 0.16);
      padding: 20px 16px;
      margin-top: 10px;
      li {
        background: transparent !important;
        border: none;
        padding: 0;
        font-size: 14px;
        line-height: 170%;
        font-weight: 400;
        &:hover {
          color: #707680;
        }
      }
    }
  }
  &__filter-btn {
    width: 20px;
    flex-shrink: 0;
    display: none;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__selections-title {
    display: none;
    font-size: 14px;
    line-height: 170%;
    font-weight: 600;
    margin-bottom: 8px;
  }
  &__selections-bottom {
    position: relative;
    display: none;
    padding: 20px 0 48px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: calc(100% + 60px);
      height: 1px;
      background: #DCE0E5;
    }
  }
  &__selections-btn {
    height: 48px;
    padding: 5px 40px;
    margin: 0 20px;
    span {
      font-size: 14px;
    }
  }
}
.filter-bg {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
  }
}

@media screen and (max-width: 1023px) {
  .page-top{
    &__selections-wrap {
      position: fixed;
      z-index: 5;
      left: 0;
      bottom: -105%;
      width: 100%;
      height: calc(100% - 150px);
      background: #fff;
      border-radius: 12px;
      padding: 28px 30px 116px 30px;
      pointer-events: none;
      transition: all 0.5s;
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #A7ABB2;
        border-radius: 3px;
        margin: 6px 0;
      }
      &::-webkit-scrollbar-thumb {
        background: #FFE500;
        border-radius: 3px;
        margin: 6px 0;
      }
      &:after {
        content: '';
        position: absolute;
        top: 12px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 24px;
        height: 4px;
        background: #A7ABB2;
        border-radius: 24px;
      }
    }
    .filter--active {
      bottom: 0;
      pointer-events: auto;
      .page-top__selections-bottom {
        bottom: 0;
      }
    }
    &__selections-title {
      display: block;
    }
    &__selections {
      flex-direction: column;
    }
    &__select {
      width: 100%;
      &:not(:last-child) {
        border-bottom: 1px solid #DCE0E5;
      }
      &--active {
        .page-top__select-show {
          i svg {
            transform: rotate(-180deg);
          }
        }
        .page-top__select-hidden {
          height: auto;
          margin: 8px 0 12px 0;
          overflow: unset;
        }
      }
      &-show {
        width: 100%;
        justify-content: space-between;
        background: transparent;
        border-radius: 0;
        padding: 12px 0;
      }
      &-hidden {
        position: static;
        width: 100%;
        height: 0;
        background: transparent;
        box-shadow: unset;
        padding: 0;
        opacity: 1;
        pointer-events: auto;
        overflow: hidden;
      }
    }
    &__selections-bottom {
      position: fixed;
      left: 0;
      bottom: -105%;
      width: 100%;
      display: flex;
      justify-content: center;
      background: #fff;
      border-radius: 0 0 12px 12px;
      transition: all 0.5s;
      &:before {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 12px;
        transform: translate(-50%, 0);
        width: 108px;
        height: 4px;
        background: #A7ABB2;
        border-radius: 14px;
      }
    }
    &__filter-btn {
      display: block;
    }
  }
}

@media screen and (max-width: 768px) {
  .breadcrumbs {
    .swiper-slide {
      &:first-child {
        margin-left: 20px;
      }
      &:last-child {
        padding-right: 32px;
        margin-right: 0 !important;
      }
    }
  }
  .page-top {
    &__group {
      flex-direction: column;
    }
    .search {
      width: 100%;
      input {
        width: 100%;
        height: 48px;
      }
    }
    &__group2 {
      gap: 8px;
      margin-top: 20px;
    }
    &__selections-wrap {
      padding: 28px 20px 116px;
    }
    &__select2-title {
      display: none;
    }
    .nice-select {
      .list {
        width: 260px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 20px;
  }
  .breadcrumbs {
    padding: 16px 0;
  }
  .page-top {
    padding: 16px 0 10px;
    &__selections-btn {
      width: 100%;
    }
  }
}
