.footer {
  background: #21262E;
  border-radius: 12px;
}
.footer-top {
  padding: 56px 0 32px;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 16px;
  }
  &__items {
    width: calc(100% - 411px);
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 16px / 2));
    gap: 32px 16px;
  }
  &__item {
    &:last-child {
      grid-column: 1/3;
    }
    &-group {
      display: grid !important;
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      align-items: start;
      gap: 16px;
    }
    &-title {
      font-size: 14px;
      line-height: 170%;
      font-weight: 500;
      color: #fff;
      pointer-events: none;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      margin-top: 16px;
      li a {
        font-size: 14px;
        line-height: 170%;
        color: #6D7A8F;
        &:hover {
          color: #fff;
        }
      }
    }
  }
  &__group {
    width: 395px;
    display: flex;
    flex-direction: column;
    align-items: start;
    background: #2A313D;
    border-radius: 12px;
    padding: 24px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    color: #fff;
  }
  &__items2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 24px;
    margin-bottom: auto;
  }
  &__item2 {
    &-title {
      font-size: 16px;
      line-height: 150%;
      color: #6D7A8F;
    }
    &-text {
      display: inline-block;
      font-size: 16px;
      line-height: 150%;
      color: #fff;
      margin-top: 8px;
      &:hover {
        color: #6D7A8F;
      }
    }
  }
  &__btn {
    height: 56px;
    padding: 5px 32px;
    margin-top: 24px;
    span {
      font-size: 16px;
    }
  }
}
.footer-bottom {
  border-top: 1px solid #2A313D;
  padding: 32px 0 56px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__text {
    font-size: 14px;
    line-height: 170%;
    color: #6D7A8F;
  }
  &__items {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #6D7A8F;
    &:hover {
      color: #fff;
      svg path {
        fill: currentColor;
      }
    }
    i {
      max-width: 22px;
      flex-shrink: 0;
      svg {
        max-width: 100%;
        width: auto;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
    span {
      font-size: 14px;
      line-height: 170%;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    &__items {
      width: calc(100% - 356px);
    }
    &__group {
      width: 340px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      flex-direction: column;
      gap: 32px;
    }
    &__items {
      width: 100%;
    }
    &__group {
      width: 100%;
    }
  }
  .footer-bottom {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__text {
      order: 1;
    }
    &__items {
      flex-direction: column;
      align-items: start;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    padding: 32px 0 20px;
    &__inner {
      gap: 20px;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__item {
      &:last-child {
        grid-column: 1/1;
      }
      &-title {
        position: relative;
        width: 100%;
        pointer-events: auto;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          transform: translate(0, -50%);
          width: 10px;
          height: 5px;
          background-image: url("../img/arrow-bottom.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          transition: all 0.3s;
        }
      }
      .item-show--active {
        &:after {
          transform: translate(0, -50%) rotate(-180deg);
        }
      }
      &-group {
        grid-template-columns: 100%;
        gap: 12px;
      }
      .item-hidden {
        display: none;
      }
    }
    &__group {
      padding: 20px;
    }
    &__subtitle {
      font-size: 14px;
    }
    &__items2 {
      gap: 12px;
      margin-top: 20px;
    }
    &__item2 {
      &-title {
        font-size: 14px;
      }
      &-text {
        font-size: 14px;
      }
    }
    &__btn {
      width: 100%;
      height: 48px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
  .footer-bottom {
    padding: 20px 0 32px;
  }
}
